<template>
    <div class="clearAllContainer">
        <span class="clearAllBtn" @click="clearTodo">Clear All</span>
    </div>
</template>
<script>
export default{
    methods:{
        clearTodo(){
            this.$emit('removeAll')
        }
    }
}
</script>

<style>
    .clearAllContainer{
        width:8.5rem;
        height:50px;
        line-height: 50px;
        background:white;
        border-radius:5px;
        margin:0 auto;
        margin-top:20px;
    }
    .clearAllBtn{
        color:#e20303;
        display:block;
    }
</style>


